<template>
    <div class="footer">
            <aplayer controls theme mode
        :music="{
            title: 'secret base~好听吗!',
            artist: 'Silent Siren',
            src: 'https://moeplayer.b0.upaiyun.com/aplayer/secretbase.mp3',
            pic: './static/img/2.jpg'
        }"
        hiegth="2rem"/>
    </div>
</template>

<script>
import Aplayer from 'vue-aplayer'
export default {
    data(){
        return{
            theme:`#fff`,
            mode:`circulation`,
        }
    },
    components:{
        Aplayer
    },
}
</script>

<style scoped>
.footer{width: 100%;height: 1.6rem;background:#fff;position: fixed;bottom: 0rem;border-top: 1px solid #ddd;z-index: 9;}
</style>





