<template>
  <div>
    <div class="tmain">
        <swipe class="my-swipe">
            <swipe-item class="slide1"></swipe-item>
            <swipe-item class="slide3"></swipe-item>
            <swipe-item class="slide4"></swipe-item>
            <swipe-item class="slide6"></swipe-item>
            <swipe-item class="slide7"></swipe-item>
            <swipe-item class="slide8"></swipe-item>
            <swipe-item class="slide9"></swipe-item>
            <swipe-item class="slide10"></swipe-item>
            <swipe-item class="slide11"></swipe-item>
            <swipe-item class="slide12"></swipe-item>
        </swipe>
        <div class="tmai1">
            <div class="tmt">
                <li class="fl" v-for="ting in tingList" :key="ting.id">
                    <router-link to="/ting/TingDetail">
                       <img :src="ting.bg" width="65%"/>
                       <p>{{ting.name}}</p>   
                    </router-link>    
                </li>
                <div class="tit">
                    <li>新歌</li>
                    <li>图片</li>
                    <li>热门</li>
                    <li>视屏</li>
                    <li>资讯</li>
                </div>
                <div>
                    <ul class="lis" >
                        <li class="lis1" v-for="songs in movieDetail" :key="songs.id" >
                          <router-link to="/ting/TingDetail">
                            <div class="lis1-ad"><a href="#">+</a></div>
                            <div class="lis1-img"><img :src="songs.img" /></div>
                            <div class="lis1-info">
                                <p>{{songs.nm}}</p>
                                <p>{{songs.dir}}<span>...</span> </p>
                                <p>{{songs.pn}}<span>刚刚</span></p>
                            </div>
                          </router-link>
                        </li> 
                        <div class="loading" v-show="loadingShow">
                            <img src="@/assets/img/loading.gif">
                        </div>
                        <div class="tip" v-show="tip">
                           <h4>--到第儿啦！！！！--</h4>
                        </div>        
                    </ul>
                </div> 
            </div>
        </div>
    </div>
  </div>
</template>
<script>
  import commonHeader from '../common/Header'
  
  import { Swipe, SwipeItem } from 'vue-swipe';
  import 'vue-swipe/dist/vue-swipe.css';

  import Axios from 'axios'

  export default {
      data(){
        return {
            tingList: [],
            movieDetail: [],
            loadingShow: false,
            tip: false,  
            
        }
    },methods: {
      loadData(){
      let url2 = "./static/data/moviedata.json";
       Axios.get(url2)
        .then(res1 => {
          this.movieDetail = res1.data.data.movies;
          // console.log(this.movieDetail);
        })
        .catch(() => {
           //alert("数据接收失败!");
        });
      }
    },
    mounted() {
       let url = "./static/data/musiclist.json"
      
       Axios.get(url).then(res => {
           this.tingList = res.data.albums;
          //  console.log(res, this.tingList);
       }).catch(); 
       this.loadData();
    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; 
      if( scrollTop+clientHeight >= scrollHeight-110){ 
        this.loadingShow = true;
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
       
      }
    }
    },
    components: {
      commonHeader,
     'swipe': Swipe,
     'swipe-item': SwipeItem,
    }

  }
</script>
<style scoped>
@import url('../../assets/css/common.css');
.tmai1{width: 100%;background: #fff;z-index: 8;margin-top: -0.4rem;position: relative;border-radius: 0.1rem;margin-bottom: 1.3rem;}
.tmt{width: 95%;display: flex;justify-content: space-evenly;flex-wrap: wrap;margin: 0.2rem;padding-top: 0.3rem;}
.tmt li{list-style: none;width: 2rem;height: 1.8rem;text-align: center;}
.tmt .fl a{color: #222;font-size: 0.3rem;}
.tmt .tit{margin-left: -0.8rem;}
.tmt .tit li{width: 0.8rem;height: 0.8rem;float: left;text-align: center;line-height: 1rem;font-size: 0.3rem;color: #555;}
.tmt .tit li:first-child{color: #111;border-bottom: 1px solid #1748D3;}
.tmt .tit li:active{color: #111;border-bottom: 1px solid #1748D3;}

.lis{width: 99%;}
.lis li{margin-top: 0.1rem;width: 6rem;height: 1.8rem;padding-left: 0.1rem;}
.lis1-ad{width: 0.35rem;height: 0.35rem;background: #fff;float: left;margin-top: 0.68rem;margin-right:0.1rem;border: 1px solid #666;border-radius: 0.1rem;line-height: 0.35rem;}
.lis1-ad a{font-size: 0.3rem;color: #666;}
.lis1-img{width: 20%;float: left;height: 70%;overflow: hidden;border-radius: 0.1rem;margin-top: 0.25rem;margin-right:0.2rem;}
.lis1-info{width: 66%;background: #fff;float: left;margin-top: 0.14rem;text-align: left;}
.lis1-info p:first-child{color: #111;font-size: 0.3rem;}
.lis1-info p{color: #888;font-size: 0.2rem;}
.lis1-info p span{float: right;}


.tmain{height: 2.6rem;margin-top: 1.78rem;background: #1D82FE;}
.my-swipe{width: 96%;height: 2.5rem;margin-left: 2%;border-radius: 0.2rem;}
/* .slide1 {
  background-image: url('/static/img/a.jpg');
}
.slide3 {
  background-image: url('/static/img/c.jpg');
}
.slide4 {
  background-image: url('/static/img/d.jpg');
}
.slide6 {
  background-image: url('/static/img/f.jpg');
}
.slide7 {
  background-image: url('/static/img/g.jpg');
}
.slide8 {
  background-image: url('/static/img/h.jpg');
}
.slide9 {
  background-image: url('/static/img/i.jpg');
}
.slide10 {
  background-image: url('/static/img/j.jpg');
}
.slide11 {
  background-image: url('/static/img/k.jpg');
}
.slide12 {
  background-image: url('/static/img/l.jpg');
} */
  
.loading{text-align: center;}
.tip{text-align: center;}
</style>


