<template>
    <div class="header"  :style="{background: bg}">
        <ul>
            <li><router-link to="/left" ><img src="../../assets/img/list.png"></router-link></li>
            <li><router-link to="/wo" >我</router-link></li>
            <li><router-link to="/ting/tingList" >听</router-link></li>
            <li><router-link to="/kan/kanList" >赏</router-link></li>
            <li><router-link to="/chang" >看</router-link></li>
            <li class="a">
               <img src="../../assets/img/list1.png">
               <div class="nlist">
                   <span></span>
                  <li><p>听歌识曲</p></li>
                  <li><p>扫一扫</p></li>
                  <li><p  @click="qrq">我的二维码</p></li>
               </div>
            </li>
        </ul>
        <div class="txt">
            <input type="text">
            <span><slot class="title" name="title">张艺兴、贾斯汀比伯...</slot></span>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        qrq(){
            this.$router.push('/qrq/Qrq')
        }
    },
    props: ['bg'],
    }
</script>
<style scoped>
.header{width:100%;height: 1.82rem;position: fixed;top: 0;background: #1D82FE;z-index: 9;}
.header ul{width: 100%;display: flex;justify-content: center;}
.header li{width: 1rem;height:0.8rem;line-height: 1.2rem;text-align: center;}
.header li a{font-size: 0.45rem;color: #F7FCFF;}
.header li a.router-link-active{color: #fff;font-size: 0.5rem; }
.header li img{width: 0.5rem;margin-top: 0.35rem;}
.header .txt{width: 5.8rem;margin: 0 auto;margin-top: 0.3rem;color: #eee;}
.header .txt span{position: relative;top:-0.5rem;left: 1.8rem;}
.header .txt input{width: 5.8rem;height: 0.5rem;border: none;box-sizing: border-box;text-align: center;line-height: 0.4rem;/*background: rgba(255, 255, 255, 0.2) url('../../assets/img/icon1.png')no-repeat 1.2rem center;background-size: 0.4rem;*/border-radius: 1rem;}
.header .txt input:focus{outline: none;}

.header .a:hover .nlist{display: block;}
.nlist{width: 40%;height: 2.5rem;background: #fff;position:absolute;z-index: 8;right: 0.2rem;margin-top: -0.1rem;border-radius: 0.1rem;padding-top: 0.08rem;display: none;}
.nlist li{width: 78%;/*background: url(../../assets/img/po.jpg)no-repeat 0.02rem center;*/text-align: left;line-height: 0.8rem;padding-left: 0.6rem;}
.nlist li p{font-size: 0.35rem;color: #222;}
.nlist span{position: absolute;width: 0;height: 0;overflow: hidden;border: 0.2rem solid transparent;border-bottom-color: #fff;top:-0.38rem;right: 0.3rem;}

</style>



