<template>
  <div class="lanl">
    <div class="fenl">
      <li class="fl" v-for="kan in kanList" :key="kan.id">
        <router-link :to="'/kan/kanDetail/'+kan.id">
          <img :src="kan.bg" width="65%" />
          <p>{{kan.name}}</p>
        </router-link>
      </li>
    </div>
    <ul class="list">
      <li v-for="(photo,index) in photoList" :key="index">
        <router-link :to="'/kan/kanDetail/'+index">
          <img :src="photo.src" width="100%" />
          <p>{{photo.nm}}<span>{{photo.sc}}</span></p>
        </router-link>
       </li>
       <div class="loading" v-show="loadingShow">
          <img src="../../assets/img/loading.gif">
       </div>
       <div class="tip" v-show="tip">
          <h4>--到第儿啦！！！！--</h4>
       </div>
    </ul>
  </div>
</template>

<script>
  import Axios from 'axios'

  export default {
    data() {
      return {
        kanList: [],
        photoList: [],
        loadingShow: false,
        tip: false, 
      }
    },
    methods:{
    loadData(){
        let url1 = "./static/data/kandata.json"

      Axios.get(url1).then(res => {
        this.kanList = res.data.albums;

      }).catch();
    }
    },
    mounted() {
      let url = './static/data/photodata.json'

      Axios.get(url).then(res => {

        this.photoList = res.data.photoData
        console.log(res.data.photoData);
        this.$store.dispatch('actionPhotoList', this.photoList);
        // console.log(this.photoList);
      }).catch()
      
    this.loadData();
    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; 
      if( scrollTop+clientHeight >= scrollHeight-110){ 
        this.loadingShow = true;
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
       
      }
    }
    }
  }
</script>

<style scoped>
  .lanl {
    margin-top: 1.7rem;
  }

  .fenl {
    width: 95%;
    display: flex;
    justify-content: space-evenly;
    margin: 0.2rem;
    padding-top: 0.3rem;
    height: 3.1rem;
  }

  .fenl li {
    list-style: none;
    width: 2rem;
    height: 1.8rem;
    text-align: center;
  }

  .fenl p {
    color: #333;
  }


  @import url('../../assets/css/common.css');

  .list {
    margin-top: -0.5rem;
    position: relative;
    bottom: 1.4rem;
  }

  .list li {
    width: 45%;
    height: 2.6rem;
    float: left;
    margin-left: 0.2rem;
    border-radius: 0.1rem;
    overflow: hidden;
  }

  .list li p {
    color: #333;
    background: url('../../assets/img/ico.jpg')no-repeat 1.8rem -0.01rem;
    background-size: 0.4rem;
  }

  .list li p span {
    float: right;
  }
  
.loading{text-align: center;}
.tip{text-align: center;}
</style>


