<template>
  <div>
    <common-header>
        <span id="sl" slot="title">输入作者、图片分类名</span>
    </common-header>
    <div>
        <v-touch class="mai" :style="styleObj" @swipeleft="swipeLeft" @swiperight="swipeRight" @swipedown="swipeDown" @tap="tap"></v-touch>
    </div>
    <common-footer></common-footer>
  </div>

</template>
<script>
import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'

export default {
     components: {
        CommonHeader,
        CommonFooter,
    },data(){
        return {
            nowIndex: 0, 
        }
    },
    mounted(){
         //console.log(this.$route.params.index); //接收route传参
        //  console.log(this.$store.state.photoList); //vuex输出
        // console.log(this.$store.state.photoList[this.$route.params.index]);
        // console.log(this.$store.state.photoList[this.$route.params.index].src);//单张图片的src
        this.nowIndex = this.$route.params.index;
        console.log('接收的参数ID= '+this.nowIndex);
    },
    computed: {
        styleObj(){
            return{
                background: `#000 url(${this.$store.state.photoList[this.nowIndex].src}) no-repeat center / contain`   
            }  
        }
    },methods: {  
        swipeLeft(){
            this.nowIndex++;
            if(this.nowIndex == this.$store.state.photoList.length ){
                this.nowIndex = 0
            }
        },
        swipeRight(){
            this.nowIndex--;
            if(this.nowIndex < 0 ){
                this.nowIndex = this.$store.state.photoList.length - 1;
            }
            console.log("right!!!");
        },
        swipeDown(){
            console.log("Down!!!");
        },
        tap(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
#sl{position: relative;top:0.01rem;left: -0.1rem;}
.mai{margin:1rem 0; position:absolute; top:0; right:0; bottom:0; left:0;background: black;color: aliceblue;}
</style>


