<template>
  <div >
    <common-header>
        <span id="sl" slot="title">想看就看，今天想看啥！</span>
    </common-header>
    <ul class="kan-list" >
            <li class="kan" v-for="(kan,index) in movieList" :key="index" >
                <div class="kan-video">
                  <video :src="kan.bg" controls width="100%"></video>
                </div>
                <div class="kan-info">
                  <p>{{kan.name}}<span>{{kan.star}}</span></p>
                </div>
            </li> 
            <div class="loading" v-show = "loadingShow">
              <img src="@/assets/img/loading.gif">
            </div> 
            <div class="tip" v-show="tip">
              <h4>--到第儿啦！！！！--</h4>  
            </div>         
        </ul>
  </div>
</template>
<script>
import CommonHeader from '../common/Header'
import Axios from "axios";

export default {
     components: {
        CommonHeader,
    }, 
    data() {
    return {
      movieList: [], 
      loadingShow: false,
      tip: false, 
    }
  },
  methods:{
    loadData(){
      let url2 = "./static/data/kanship.json";

      Axios.get(url2)
        .then(res => {
          let list = res.data.albums;
          let data = list.slice(
                this.movieList.length, 
                this.movieList.length +3 );

          if(data.length < 3){
            this.tip = true;
          }
          this.movieList = this.movieList.concat(data);
        })
        .catch(() => {
          alert("数据接收失败!");
        });
    },
  },                         
  mounted(){
    this.loadData();
    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; 
      if( scrollTop+clientHeight >= scrollHeight-110){ 
        this.loadingShow = true;
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
       
      }
    }
  }
}
</script>
<style scoped>
#sl{position: relative;top:0.01rem;left: -0.1rem;}
.kan-list{ margin-top: 1.8rem;background: #FCFDFD;padding-top: 0.2rem;}
.kan{width: 96%;height: 4.4rem;border-radius: 0.1rem;overflow: hidden;margin-left: 0.13rem;}
.kan-info{width: 90%;height: 0.6rem;padding-left:0.5rem;font-size: 0.3rem;color: #222;line-height: 0.4rem;background: url('../../assets/img/ico1.jpg')no-repeat left -0.01rem;background-size: 0.4rem;border-bottom: 0.01rem dashed #444;}
.kan-info p span{font-size: 0.4rem;color: #111;float: right;}
.loading{text-align: center;}
.tip{text-align: center;}


</style>




