import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Left from '@/components/left/Left'
import Ting from '@/components/ting/Ting'
import TingList from '@/components/ting/TingList'
import TingDetail from '@/components/ting/TingDetail'
import Wo from '@/components/wo/Wo'
import Kan from '@/components/kan/Kan'
import KanList from '@/components/kan/KanList'
import KanDetail from '@/components/kan/KanDetail'
import Chang from '@/components/chang/Chang'
import Qrq from '@/components/qrq/Qrq'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { 
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/ting/tingList'
    },{
      path:'/ting',
      name:'Ting',
      component:Ting,
      children:[{
        path:'tingList',
        name:'TingList',
        component:TingList,         
      },{
        path:'tingDetail',
        name:'TingDetail',
        component:TingDetail,         
      }]
    },{
      path:'/kan',
      name:'Kan',
      component:Kan,
    },{
      path:'/chang',
      name:'Chang',
      component:Chang,
    },{
      path:'/wo',
      name:'Wo',
      component:Wo,
    },{
      path: '/kan',
      name: 'Kan',
      component: Kan,
      redirect: '/kan/kanList',
      children: [{
        path: 'kanList',
        component: KanList,
      }, {
        path: 'kanDetail/:index',       
        component: KanDetail

      }]
    },{
        path:'/left',
        name:'Left',
        component:Left,
    },{
      path:'/qrq/Qrq',
      name:'Qrq',
      component:Qrq,
    }
  ]
})



